html {
	box-sizing: border-box
}

*,
*:before,
*:after {
	box-sizing: inherit
}

body {
	font: 16px Lato, 'Helvetica Neue', Helvetica, Arial, sans-serif;
	color: #ddd;
	background-color: #333;
	-webkit-box-shadow: inset 0 3px 30px rgba(0, 0, 0, 0.3);
	-moz-box-shadow: inset 0 3px 30px rgba(0, 0, 0, 0.3);
	box-shadow: inset 0 3px 30px rgba(0, 0, 0, 0.3);
	text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
	-webkit-font-feature-settings: "kern" 1;
	-moz-font-feature-settings: "kern" 1;
	-o-font-feature-settings: "kern" 1;
	font-feature-settings: "kern" 1;
	font-kerning: normal;
	margin: 0
}

.clear {
	display: block
}

.clear:after {
	content: " ";
	display: block;
	height: 0;
	clear: both;
	visibility: hidden
}

header,
section,
footer {
	float: left;
	width: 100%;
	clear: both
}

header {
	padding: 15px;
	background: #2b2b2b
}

header h1,
header nav {
	display: inline-block
}

header .flexbox {
	display: flex;
	height: 50px
}

header .flexbox>* {
	margin: auto
}

header .logo {
	display: block
}

header .logo img {
	margin-top: -7px
}

header .search .svg-icons {
	display: none
}

nav ul,
.meta ul {
	padding: 0;
	margin: 0;
	white-space: nowrap;
	display: inline-block
}

nav li,
.meta li {
	display: inline-block
}

.meta ul {
	margin-left: 10px
}

.meta ul li {
	vertical-align: middle
}

.main-nav li a,
.meta li a {
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
	font-weight: 900;
	font-size: 0.75em;
	padding: 0.5em 1em;
	text-shadow: none;
	text-transform: uppercase;
	-webkit-transition: all 0.25s;
	-moz-transition: all 0.25s;
	-o-transition: all 0.25s;
	transition: all 0.25s
}

.main-nav li a:hover,
.meta li a:hover {
	background-color: #252525;
	-webkit-box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5), 0 1px 0 rgba(255, 255, 255, 0.1);
	-moz-box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5), 0 1px 0 rgba(255, 255, 255, 0.1);
	box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5), 0 1px 0 rgba(255, 255, 255, 0.1);
	text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5)
}

.main-nav li.current a,
.meta li.current a {
	background-color: #fc0;
	color: #222;
	-webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), 0 1px 5px rgba(0, 0, 0, 0.5);
	-moz-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), 0 1px 5px rgba(0, 0, 0, 0.5);
	box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), 0 1px 5px rgba(0, 0, 0, 0.5);
	text-shadow: 0 1px 0 rgba(255, 255, 255, 0.3)
}

.mobile-nav {
	padding: 0 5px
}

.mobile-nav ul {
	overflow: hidden;
	width: 100%;
	display: table
}

.mobile-nav a {
	float: left;
	width: 100%;
	background-color: #333;
	color: #fc0;
	text-align: center;
	text-transform: uppercase;
